import {
  IonCard,
  IonCardContent,
  IonCardHeader,
  IonCardSubtitle,
  IonCardTitle,
  IonCol,
  IonContent,
  IonGrid,
  IonHeader,
  IonImg,
  IonItem,
  IonNavLink,
  IonPage,
  IonRouterLink,
  IonRow,
  IonText,
  IonTitle,
  IonToolbar,
} from "@ionic/react";
import React from "react";
import "./Home.css";
import danielSheaPic from "../Assets/danielshea2.png";
import studyingPic from "../Assets/studying2.png";

const Home: React.FC = () => {
  return (
    <IonPage>
      <IonHeader>
        <IonToolbar className="home-toolbar">
          <IonTitle>
            <IonText className="heading1-text">Home</IonText>
          </IonTitle>
        </IonToolbar>
      </IonHeader>
      <IonContent className="home-content">
        <IonGrid className="home-grid">
          <IonRow className="home-row ion-justify-content-center">
            <IonCol className="home-col">
              <IonCard className="home-card">
                <IonCardHeader>
                  <IonCardTitle>
                    <IonText className="heading2-text">Daniel Shea</IonText>
                  </IonCardTitle>
                  <IonCardSubtitle>
                    <IonText className="subtitle1-text">
                      Working towards a Bachelors of Science in Computer Science
                      with a Business Administration minor at{" "}
                      <IonRouterLink
                        href="https://www.csc.ncsu.edu/"
                        target="_blank"
                        rel="noreferrer"
                      >
                        North Carolina State University
                      </IonRouterLink>
                    </IonText>
                  </IonCardSubtitle>
                  <IonCardContent>
                    <IonGrid>
                      <IonRow>
                        <IonCol>
                          <IonText className="text-1">
                            I am studying Computer Science at NC State - I like
                            reading about AI, creating algorithms using Google
                            Circ, and creating apps!
                          </IonText>
                        </IonCol>
                      </IonRow>
                      <IonRow>
                        <IonCol>
                          <IonRouterLink
                            href="https://www.linkedin.com/in/daniel-g-shea/"
                            target="_blank"
                          >
                            <IonItem className="link-item" color="secondary">
                              <IonText>LinkedIn</IonText>
                            </IonItem>
                          </IonRouterLink>
                        </IonCol>
                      </IonRow>
                    </IonGrid>
                  </IonCardContent>
                </IonCardHeader>
              </IonCard>
            </IonCol>
          </IonRow>
          <IonRow className="home-row ion-justify-content-center">
            <IonCol className="home-col">
              <IonCard className="home-card">
                <IonCardContent>
                  <IonGrid>
                    <IonRow>
                      <IonCol>
                        <IonText className="text-1">
                          Please excuse the lack of content on this site... I am
                          currently studying for exams. Check back during
                          wintertime for a more interactive website!
                        </IonText>
                      </IonCol>
                    </IonRow>
                    <IonRow>
                      <IonCol>
                        <IonImg src={studyingPic} className="studying-pic" />
                      </IonCol>
                    </IonRow>
                  </IonGrid>
                </IonCardContent>
              </IonCard>
            </IonCol>
          </IonRow>
        </IonGrid>
      </IonContent>
    </IonPage>
  );
};

export default Home;
